'use client';

import { useState } from 'react';
import { useUserStore } from '@/store/useUserStore';
import { useSimuladorStore } from '@/store/useSimuladorStore';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Pencil, Trash2, Plus } from 'lucide-react';
import { PatrimonioItem } from '@/types/patrimonioItem';

export default function PatrimonioPage() {
  const { idade } = useUserStore();
  const { ano } = useSimuladorStore();
  const [patrimonios, setPatrimonios] = useState<PatrimonioItem[]>([]);
  const [modoCadastro, setModoCadastro] = useState(false);
  const [novaPropriedade, setNovaPropriedade] = useState('');
  const [novoValor, setNovoValor] = useState('');

  const adicionarPatrimonio = () => {
    const valorConvertido = Number(novoValor);
    if (!novaPropriedade.trim() || isNaN(valorConvertido)) {
      alert('Preencha corretamente os dados.');
      return;
    }
    setPatrimonios([...patrimonios, { propriedade: novaPropriedade, valor: valorConvertido }]);
    setNovaPropriedade('');
    setNovoValor('');
    setModoCadastro(false);
  };

  const removerPatrimonio = (index: number) => {
    setPatrimonios(patrimonios.filter((_, i) => i !== index));
  };

  const total = patrimonios.reduce((acc, p) => acc + p.valor, 0);

  return (
    <main className="min-h-screen bg-gradient-to-br from-yellow-50 to-white p-4 flex items-center justify-center">
      <div className="w-full max-w-4xl">
        <Card>
          <div className="flex justify-between items-center mb-4">
            <h1 className="text-2xl font-bold">Patrim√¥nio</h1>
            <button onClick={() => setModoCadastro(true)} className="text-green-600 hover:text-green-800">
              <Plus className="inline-block mr-1" /> Novo Cadastro
            </button>
          </div>

          {modoCadastro && (
            <div className="flex flex-col md:flex-row gap-2 mb-4">
              <input
                type="text"
                placeholder="Propriedade (Ex: Casa, Carro...)"
                className="border px-3 py-2 rounded-md w-full"
                value={novaPropriedade}
                onChange={(e) => setNovaPropriedade(e.target.value)}
              />
              <input
                type="number"
                placeholder="Valor"
                className="border px-3 py-2 rounded-md w-full"
                value={novoValor}
                onChange={(e) => setNovoValor(e.target.value)}
              />
              <Button onClick={adicionarPatrimonio} className="bg-green-500 hover:bg-green-600 text-white">
                Adicionar
              </Button>
            </div>
          )}

          <table className="w-full border border-gray-300 rounded-xl overflow-hidden text-left">
            <thead className="bg-yellow-100 text-yellow-900">
              <tr>
                <th className="px-4 py-2">Ano</th>
                <th className="px-4 py-2">Idade</th>
                <th className="px-4 py-2">Propriedade</th>
                <th className="px-4 py-2">Valor</th>
                <th className="px-4 py-2">Editar</th>
                <th className="px-4 py-2">Excluir</th>
              </tr>
            </thead>
            <tbody>
              {patrimonios.map((item, i) => (
                <tr key={i} className="odd:bg-white even:bg-yellow-50">
                  <td className="px-4 py-2">{ano}</td>
                  <td className="px-4 py-2">{idade}</td>
                  <td className="px-4 py-2">{item.propriedade}</td>
                  <td className="px-4 py-2 text-green-700 font-semibold">
                    {item.valor.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
                  </td>
                  <td className="px-4 py-2 text-center">
                    <Pencil className="w-5 h-5 text-indigo-500 cursor-pointer" />
                  </td>
                  <td className="px-4 py-2 text-center">
                    <Trash2 onClick={() => removerPatrimonio(i)} className="w-5 h-5 text-red-500 cursor-pointer" />
                  </td>
                </tr>
              ))}
            </tbody>
            <tfoot>
              <tr className="bg-yellow-100 font-semibold">
                <td colSpan={3} className="px-4 py-2 text-right">
                  Total:
                </td>
                <td className="px-4 py-2 text-green-700">
                  {total.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
                </td>
                <td colSpan={2}></td>
              </tr>
            </tfoot>
          </table>
        </Card>
      </div>
    </main>
  );
}
